import axios from 'axios'
import React, { useState, useEffect } from 'react'
import MyTimer from './MyTimer'
// import Sidebar from './Sidebar'


const Project = () => {

  // const [myproject, setMyProject] = useState()

  const [myproject,setMyProject] = useState([])

  useEffect(
    () => {
      fetchEmployee()
    }
  )

  const fetchEmployee = () => {
    const data = { "Userid": sessionStorage.getItem("userid"),"Name":sessionStorage.getItem("name") }
    axios.post("/api/myproject", data)
      .then(
        (res) => {
          setMyProject(res.data)
        }
      )
      .catch(
        (err) => {
          console.log(err);
        }
      )
  }


  const deleteProject = async (id) => {
    await axios.delete(`/api/project/${id}`)
      .then(
        (res) => {
          alert("Deleted Successfully")
          res.json()
        }
      )
      .catch(
        (err) => {
          console.log(err)
        }
      )
  }


  const time = sessionStorage.getItem("time")


  return (
    <>
      {/* <Sidebar /> */}
      <div className="container">
        <div className="col col-12 col-sm-12 col-md-12 col-lg-12 col-xl-12 col-xxl-12">
          <div className="row">
            <div className="row g-3 ">
              <h2>Projects</h2>
              <div className="col col-12 col-sm-12 col-md-12 col-lg-12 col-xl-12 col-xxl-12">

                <table class="table table-striped table-primary">
                  <thead>
                    <tr>
                      <th scope="col">Project</th>
                      <th scope="col">Task</th>
                      <th scope="col">Job Description</th>
                      <th scope="col">Start Time</th>
                      <th scope="col">Time</th>
                      <th scope="col"></th>
                      <th scope="col">Delete</th>




                    </tr>
                  </thead>

                  {
                    myproject.map(
                      (value, key) => {
                        return <tbody>
                          <tr>
                            <td>{value.Projectname}</td>
                            <td>{value.Task}</td>
                            <td>{value.JobDescription}</td>
                            <td>{value.AddedDate}</td>
                            <td>{time}</td>
                            {/* <td>{value.Duration}</td> */}
                            <td><MyTimer /></td>
                            {/* <td>
                          <button className="btn btn-primary">Start</button>
                          </td> */}
                            <td>
                            <button className="btn btn-danger" onClick={() => deleteProject(value._id)}>Delete</button>
                            </td>



                          </tr>
                        </tbody>

                      }
                    )
                  }


                </table>

              </div>
            </div>
          </div>
        </div>
      </div>
    </>

  )
}

export default Project